import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import styled from "styled-components"

const Container = styled.header`
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  background-color: rebeccapurple;
  justify-content: space-between;
  width: 100%;
  height: 60px;
  padding: 0 5%;
`
const IconContainer = styled.i`
  margin: auto 5px;
`

const Header = ({ siteTitle }) => (
  <Container>
    <Link
      to="/"
      style={{
        color: `white`,
        textDecoration: `none`,
      }}
    >
      <div>{siteTitle}</div>
    </Link>
    <section className="icon-list">
      <a
        href="http://twitter.com/leogouveia"
        alt="Twitter Link"
        target="_blank"
        rel="noopener noreferrer"
      >
        <IconContainer className="nes-icon twitter"></IconContainer>
      </a>
      <a
        href="http://github.com/leogouveia"
        alt="Github Link"
        target="_blank"
        rel="noopener noreferrer"
      >
        <IconContainer className="nes-icon github"></IconContainer>
      </a>
      <a
        href="https://www.linkedin.com/in/leonardo-gouveia/"
        alt="Linkedin Link"
        target="_blank"
        rel="noopener noreferrer"
      >
        <IconContainer className="nes-icon linkedin"></IconContainer>
      </a>
    </section>
  </Container>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
